// common variables
$primary-medium: #005888;
$primary-dark: #024e79;
$background-gray: #efefef;

$main-test-card-white: #f9f9f9;

// button styles
button.btn-primary {
  background-color: $primary-medium;
  border-color: $primary-medium;
}

.WBBCard {
  border-radius: 5px;
  .card-header {
    background-color: $primary-medium;
    color: white;
  }
  .card-header:first-child {

  }
  h2 {
    margin-bottom: 0;
  }
  .card-body {
    background-color: $background-gray;
  }
}

.WBBMainTestCard {
  .WBBLatencySummary {
    background-color: $main-test-card-white;
    p {
      margin-bottom: 0;
    }
  }
}
